@-webkit-keyframes swaying {
	0% {-webkit-transform: translateX(-10px) rotate(-2deg);transform: translateX(-10px) rotate(-2deg);}
	25% {-webkit-transform: translateX(10px) rotate(2deg);transform: translateX(10px) rotate(2deg);}
	50% {-webkit-transform: translateX(-10px) rotate(-2deg);transform: translateX(-10px) rotate(-2deg);}
	75% {-webkit-transform: translateX(10px) rotate(2deg);transform: translateX(10px) rotate(2deg);}
	100% {-webkit-transform: translateX(-10px) rotate(-2deg);transform: translateX(-10px) rotate(-2deg);}
}

@keyframes swaying {
	0% {-webkit-transform: translateX(-10px) rotate(-2deg);transform: translateX(-10px) rotate(-2deg);}
	25% {-webkit-transform: translateX(10px) rotate(2deg);transform: translateX(10px) rotate(2deg);}
	50% {-webkit-transform: translateX(-10px) rotate(-2deg);transform: translateX(-10px) rotate(-2deg);}
	75% {-webkit-transform: translateX(10px) rotate(2deg);transform: translateX(10px) rotate(2deg);}
	100% {-webkit-transform: translateX(-10px) rotate(-2deg);transform: translateX(-10px) rotate(-2deg);}
}




